.grid-container {
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 900px;
  margin: 5% auto;
  padding: 10px;
  color: white;
  display: grid;
  grid-template-columns: 180px 180px 180px 180px 180px;
  grid-template-rows: 180px 180px;
}

@media only screen and (max-width: 900px) {
  .grid-container {
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 900px;
    margin: 5% auto;
    padding: 10px;
    color: white;
    display: grid;
    grid-template-columns: 180px 180px ;
    grid-template-rows: 180px 180px 180px 180px 180px;
  }
}

.grid-container > div {
  text-align: center;
  margin: 10px;
}

.thumb {
  width: 130px;
  -webkit-box-shadow: 6px 6px 3px #222222;
          box-shadow: 6px 6px 3px #222222;
}

.firstthumb {
  width: 320px;
  -webkit-box-shadow: 6px 6px 3px #222222;
          box-shadow: 6px 6px 3px #222222;
}

.first {
  grid-row-start: 1;
  grid-row-end: 3;
  grid-column-start: 1;
  grid-column-end: 3;
}

.row {
  margin: 0 auto;
  text-align: center;
  height: 1.5em;
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  width: 150px;
}

.error {
  text-align: center;
  color: white;
}



